.link {
	color: #2274a5;
	text-decoration: underline;
	font-weight: 400;

	&.-md {
		font-size: 1.7rem;
		letter-spacing: .031rem;
	}

	&.-lg {
		font-size: 2.4rem;
	}

	&.-bold {
		font-weight: 700;
	}

	&.-x-bold {
		font-weight: 900;
		font-family: Lato-Bold, sans-serif;
	}

	&.-underline-none {
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&:hover {
		text-decoration: none;
		color: #2274a5;
	}
}
