/* purgecss start ignore */

$header-height: 8rem;
$header-bg-violet: #fff;

.c-header {
	// height: $header-height;

	.navbar-brand img {
		width: 78px;

		@include media-breakpoint-up(md) {
			width: 98px;
		}
	}

	.navbar {
		padding: 1.2rem 0;
	}

	.navbar-nav .nav-link {
		padding: 1.2rem;
		color: $body-color;
	}

	.nav-link.dropdown-toggle::after {
		display: none;

		svg,
		img,
		i {
			margin: 0 .4rem;
		}
	}

	.navbar-nav .dropdown-menu {
		border-radius: 0;
		box-shadow: none;
		border: none;
		width: 100vw;
		// height: 100vh;
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 0;

		@include media-breakpoint-up(md) {
			width: 360px;
			height: auto;
			position: absolute;
			top: auto;
			left: auto;
			right: 0;
			bottom: auto;
			border: 1px solid $b-color;
			border-radius: $b-radius;
			box-shadow: $b-shadow;
		}

		@include media-breakpoint-up(lg) {
			width: 360px;
			height: auto;
			position: absolute;
			top: auto;
			right: auto;
			left: 50%;
			transform: translate(-50%, 0);
			bottom: auto;
			border: 1px solid $b-color;
			border-radius: $b-radius;
			box-shadow: $b-shadow;
		}
	}

	.dropdown-item {
		padding: 1.2rem 2.4rem;
		color: #7f7e79;
		font-size: 1.7rem;
		line-height: 2.2rem;
		display: flex;
		align-items: center;
		font-weight: 400;

		&:hover {
			background-color: #e4e4e4;
		}

		&.active {
			padding-right: 6rem;
			background-color: initial;
			background-color: #e4e4e4;
			font-weight: 700;
			color: $body-color;
			position: relative;

			&::after {
				content: '';
				position: absolute;
				top: 50%;
				right: 4.4rem;
				margin-top: -.7rem;
				background: url(../img/icons/check-mark.svg) center no-repeat;
				background-size: cover;
				width: 1.4rem;
				height: 1.4rem;
			}
		}

		img {
			margin-right: .7rem;
		}
	}

	.navbar-toggler {
		border: transparent;
		outline: none;
		box-shadow: none;
		padding: 0;
	}

	.dropdown-header {
		font-size: 2.4rem;
		font-weight: 700;
		line-height: 3.2rem;
		color: $body-color;
		padding: 1.6rem 2.4rem;
		margin: 0;
		position: relative;

		@include media-breakpoint-up(lg) {
			margin-top: .8rem;
		}

		p {
			font-size: 1.4rem;
			font-weight: 400;
			font-family: Lato, sans-serif;
		}

		.text-gray {
			color: #8b929d;
		}

		.close {
			cursor: pointer;
			display: inline-flex;
			padding: .8rem;

			&:hover {
				opacity: .8;
			}

			@include media-breakpoint-up(md) {
				display: none;
			}
		}
	}

	.dropdown-content {
		padding: 0 2.4rem;

		.text-gray {
			color: #8b929d;
		}
	}

	.dropdown-divider {
		margin: 0 2.4rem;
	}
}

.navbar-nav .nav-item:last-child .nav-link {
	padding-right: 0;
}

/* purgecss end ignore */
