// $body-bg: #000;
$body-color: #252b33;

$grid-gutter-width: 16px;

@media (max-width: 767px) {
	.row {
		margin-left: -6px !important;
		margin-right: -6px !important;
	}

	.row > div[class^='col-'] {
		padding-left: 6px;
		padding-right: 6px;
	}
}

body section,
body > header {
	padding: 0 18px;

	@media all and (min-width: 768px) {
		padding: 0;
	}
}

