html {
	font-size: $initial-font-size;

	&.no-scroll {
		overflow: hidden;
		height: 100%;
		position: relative;

		body {
			overflow: hidden;
			height: 100%;
			position: relative;
		}
	}
}

body {
	font-size: 1.4rem;
	margin: 0;
	font-family: $font-stack;
	text-rendering: optimizeLegibility;
	color: $body-color;
	overflow-x: hidden;
}

button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	box-shadow: none !important;
	outline: none !important;
}

a {
	cursor: pointer;
	box-shadow: none !important;
	outline: none !important;
}

main {
	flex-grow: 1;
}

@include placeholder {
	color: rgba($black, .6);
}

@include selection {
	background-color: #2274a5;
	color: $white;
}

strong {
	font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 700;
	// font-family: $font-heading, sans-serif;
}

.h1 {
	font-size: 2.6rem;
	line-height: 3.4rem;

	@include media-breakpoint-up(md) {
		font-size: 3.6rem;
		line-height: 4.4rem;
	}
}

.h2 {
	//
}

.h3 {
	font-size: 2.4rem;
	line-height: 3.2rem;
}

.form-row {
	margin-right: -4px;
	margin-left: -4px;
}

.form-row > .col,
.form-row > [class*='col-'] {
	padding-left: 4px;
	padding-right: 4px;
}

.modal-dialog {
	@include media-breakpoint-up(xs) {
		max-width: 560px;
	}
}
