.page--home {
	.h1 {
		font-family: Lato-Black, sans-serif;
		margin: 3.2rem 0;
		font-weight: 900;
		font-size: 2.6rem;
		line-height: 3.8rem;

		@include media-breakpoint-up(md) {
			margin: 5.6rem 0;
			line-height: 4.4rem;
			font-size: 3.6rem;
		}
	}

	.no-gutters > .col,
	.no-gutters > [class*='col-'] {
		@media screen and (max-width: 576px) {
			padding-left: 6px;
			padding-right: 6px;
		}
	}

	.height-44-lg {
		@include media-breakpoint-up(md) {
			height: 4.4rem !important;
		}
	}

	.flex-aligned-center {
		display: flex;
		align-items: center;
		justify-content: center;

		@include media-breakpoint-up(sm) {
			display: inline-flex;
		}
	}

	#datepicker {
		padding: 0;
		@include media-breakpoint-up(sm) {
			padding: .4rem;
		}

		.datepicker-inline,
		.table-condensed {
			width: 100%;
		}
	}

	.datepicker > .datepicker {
		padding: 0;
	}

	.datepicker.dropdown-menu {
		padding: 0;

		@include media-breakpoint-up(sm) {
			padding: 4rem;
			margin-left: -5rem;
		}
	}

	.text-gray {
		color: #7f7e79;
	}

	.font-light {
		font-family: Lato-Light, sans-serif;
	}

	.modal {
		-webkit-overflow-scrolling: touch;
	}

	.modal-header {
		border: none;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: 1.6rem 2.4rem 1.6rem 1.6rem;

		@include media-breakpoint-up(sm) {
			padding: 1.6rem;
		}
	}

	.modal-footer {
		border: none;
		padding: 1.6rem;
	}

	.modal-body {
		padding: 1.6rem 2.4rem 2.4rem;

		@include media-breakpoint-up(sm) {
			padding: 1.6rem 3.2rem 3.2rem;
		}

		&.-height-auto {
			flex: 0 1 auto;
		}
	}

	.modal-dialog {
		margin: 0;

		@include media-breakpoint-up(sm) {
			margin: 1.75rem auto;
		}
	}

	.modal-content {
		border: none;
		min-height: 100vh;

		@include media-breakpoint-up(sm) {
			min-height: auto;
			border: 1px solid rgba(0, 0, 0, .2);
		}
	}

	.modal-title-block {
		color: #7f7e79;
		font-size: 1.7rem;
		line-height: 2.4rem;

		&--sm {
			color: #7f7e79;
			font-size: 1.4rem;
			line-height: 1.7rem;
			font-weight: 700;
		}
	}

	.modal-menu {
		&__item {
			font-size: 1.7rem;
			font-weight: 300;
			line-height: 2.1rem;
			padding: 1.2rem 2.4rem;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			color: #7f7e79;
			text-decoration: none;
			position: relative;

			&:hover,
			&:focus,
			&:active {
				background-color: #e4e4e4;
			}

			&.active {
				background-color: #e4e4e4;

				&::after {
					content: '';
					position: absolute;
					top: 50%;
					right: 4.4rem;
					margin-top: -.7rem;
					background: url(../img/icons/check-icon.png) center no-repeat;
					background-size: cover;
					width: 1.4rem;
					height: 1.4rem;
				}
			}
		}

		&.-lg {
			.modal-menu__item {
				padding: 2rem 2.4rem;

				&.active {
					color: $body-color;
					font-weight: 700;
				}
			}
		}
	}

	.form-inline {
		flex-flow: row nowrap;
		align-items: flex-start;
	}

	.form-inline-xs-100 {
		@media screen and (max-width: 546px) {
			width: 100%;
		}
	}

	.input-group .input-group-text {
		background-color: $white;
		padding: .5rem 1.6rem;
	}

	.input-group > .custom-select:not(:first-child),
	.input-group > .form-control:not(:first-child) {
		border-left: none;
		padding-left: 0;
	}

	.buy-card-img {
		width: 100%;
		max-width: 6.4rem;
		margin-top: 1.3rem;
		margin-right: .5rem;

		@include media-breakpoint-up(md) {
			max-width: 9.6rem;
			margin-top: 1.4rem;
			margin-right: auto;
		}
	}

	.buy-card p {
		line-height: 2rem;
		margin-left: -.7rem;

		@include media-breakpoint-up(sm) {
			margin-left: 0;
		}
	}

	.signIn-notice {
		border-top: 1px solid #bfbdb6;
		border-bottom: 1px solid #bfbdb6;
		background-color: $white;
		font-family: Lato-Light, sans-serif;
		padding: 1.6rem 2.4rem;
		display: flex;
		margin: 0 -2.75rem 3.2rem;

		@include media-breakpoint-up(sm) {
			margin: 2.2rem 0 4rem;
			border: 1px solid $b-color;
			border-radius: $b-radius;
			align-items: center;
		}

		> img {
			width: 28px;
			height: 28px;

			@include media-breakpoint-up(sm) {
				width: 32px;
				height: 32px;
			}
		}
	}

	.scrollable-area:not(.row) {
		display: flex;
		flex-direction: row;
		overflow: hidden;
		margin: 0 -2.7rem;

		@include media-breakpoint-up(md) {
			margin: 0 auto;
		}

		> div {
			padding: 0 2.7rem;
			width: 1120px;
			white-space: nowrap;
			overflow-x: auto;

			@include media-breakpoint-up(md) {
				padding: 0;
			}
		}

		.simple-card:last-child {
			margin-right: 0;
		}
	}

	.cards-area-h150 {
		> div {
			//
		}

		.card-img-top {
			// max-height: 100px;

			@include media-breakpoint-up(sm) {
				max-height: 170px;
			}
		}

		.card-img-wrap {
			height: 100px;

			@include media-breakpoint-up(sm) {
				height: 170px;
			}
		}
	}

	.cards-area-h300 {
		> div {
			// height: 36rem;
			display: block;

			@include media-breakpoint-up(md) {
				display: flex;
			}

			[class*='col-'] {
				display: inline-block;
				vertical-align: top;

				@include media-breakpoint-up(md) {
					display: flex;
				}
			}

			.card-img-top {
				// max-height: 192px;

				@include media-breakpoint-up(sm) {
					max-height: 263px;
				}

				@include media-breakpoint-up(xl) {
					height: 100%;
					width: auto;
				}
			}

			.card-img-wrap {
				height: 192px;

				@include media-breakpoint-up(sm) {
					height: 263px;
				}
			}
		}

		&.cards-area-h150-xs {
			.card-img-wrap {
				@media screen and (max-width: 546px) {
					height: 100px;
				}
			}
		}
	}

	.simple-card {
		width: 15rem;
		margin: 0 .9rem 0 0;
		display: inline-block;
		border-radius: $b-radius;
		text-decoration: none;
		border: none;
		overflow: hidden;

		@include media-breakpoint-up(sm) {
			margin: 0 1.3rem 0 0;
		}

		.card-img {
			background-position: center;
			background-repeat: no-repeat;
			background-size: cover;
			height: 150px;
		}

		.card-img-overlay {
			background: linear-gradient(180deg, transparent 0%, #2c2c2c 100%);
		}

		.card-title {
			font-size: 1.7rem;
			line-height: 1;
			letter-spacing: .031rem;
		}
	}

	.card-img-wrap {
		overflow: hidden;
		position: relative;
		border-radius: $b-radius;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;

		.card-img-top {
			width: auto;
			max-width: none;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}

	.custom-card {
		border: none;
		text-decoration: none;
		color: inherit;
		margin-bottom: 2.4rem;
		width: 100%;

		&:hover {
			.card-subtitle {
				// opacity: .8;
			}
		}

		@include media-breakpoint-up(sm) {
			display: block;
		}

		.card-img-top {
			border-radius: $b-radius;
			width: 100%;

			@include media-breakpoint-up(sm) {
				height: 100%;
				width: auto;
			}

			@include media-breakpoint-up(xl) {
				height: auto;
				width: 100%;
			}
		}

		.card-body {
			padding: .4rem 0;
		}

		.card-title {
			color: #335c67;
			font-size: 1.2rem;
			font-weight: 900;
			line-height: 1.7rem;
			letter-spacing: .022rem;
			text-transform: uppercase;
			margin-bottom: .4rem;
		}

		.card-subtitle {
			color: #252b33 !important;
			font-size: 1.4rem;
			font-weight: 900;
			line-height: 2rem;
			letter-spacing: .031rem;
			max-height: 4rem;
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: normal;
			text-decoration: none;

			@include media-breakpoint-up(sm) {
				font-size: 1.7rem;
				font-weight: 900;
				line-height: 2.1rem;
				max-height: 4.2rem;
			}
		}

		.card-text {
			font-size: 1.3rem;
			font-weight: 300;
			line-height: 1.8rem;
			margin-top: .2rem;
			white-space: normal;
			font-family: Lato-Light, sans-serif;
		}

		.card-link {
			//
		}
	}

	.card-icon {
		display: flex;
		flex-direction: row;
		margin-bottom: 2.4rem;

		// margin-bottom: 3.2rem;

		@include media-breakpoint-up(sm) {
			margin-bottom: 3.2rem;
		}

		&__icon {
			min-width: 1.8rem;
			flex: 0 0 auto;
			padding: 0 2rem;

			&.-map {
				img,
				svg {
					height: 4.8rem;

					@include media-breakpoint-up(sm) {
						height: auto;
					}
				}
			}

			@include media-breakpoint-up(sm) {
				padding: 0 1.6rem;
			}

			img,
			svg {
				width: 4.8rem;
				margin-top: .7rem;

				@include media-breakpoint-up(sm) {
					width: 7.2rem;
					max-height: 7.2rem;
				}
			}
		}

		&__title {
			font-size: 1.7rem;
			font-weight: 700;
			line-height: 2.1rem;
			margin-bottom: .8rem;

			@include media-breakpoint-up(sm) {
				font-size: 2rem;
				line-height: 2.4rem;
				font-weight: 400;
				margin-bottom: 0;
			}
		}

		&__text {
			word-break: break-word;
			line-height: 2.4rem;

			p {
				font-family: Lato-Light, sans-serif;
				margin-bottom: 0;

				@include media-breakpoint-up(sm) {
					font-family: Lato-Regular, sans-serif;
				}
			}
		}
	}

	.bg-black {
		background-color: #252b33;
	}

	.bg-black * {
		color: $white;
	}

	.bg-gray {
		background-color: #7f7e79;
	}
}

.bg-black {
	background-color: #252b33;

	.card-icon__text p {
		line-height: 2rem;

		@include media-breakpoint-up(sm) {
			line-height: 2.4rem;
		}
	}
}

.main-search-form {
	margin-bottom: 3.2rem;

	@include media-breakpoint-up(md) {
		margin-bottom: 5.9rem;
	}

	.col-auto {
		margin-bottom: 1.6rem;

		@include media-breakpoint-up(md) {
			margin-bottom: 0;
		}
	}

	&.-xs {
		.form-control,
		.btn {
			height: 5.6rem;
		}

		.btn-input {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			padding-left: 1.6rem;
			padding-right: 1.6rem;
			font-size: 1.7rem;
			font-weight: 700;
		}
	}

	.dropdown.show > button {
		border-color: #2274a5;
	}
}

.main-search-form .form-row {
	// width: 100%;
	width: auto;

	@include media-breakpoint-up(lg) {
		flex-flow: row nowrap;
	}
	@include media-breakpoint-up(xl) {
		width: calc(100% + 16px);
	}

	> .col-auto {
		margin-bottom: .8rem;

		@include media-breakpoint-up(lg) {
			margin-bottom: 0;
		}
	}

	.col-search {
		// flex: 0 0 45%;
		flex: 0 0 100%;
		position: relative;

		@include media-breakpoint-up(lg) {
			flex: 0 0 calc(38%);
		}
		@include media-breakpoint-up(xl) {
			flex: 0 0 calc(79.5% - 368px);
		}
	}

	.col-calendar {
		// flex: 0 0 20%;
		flex: 0 0 100%;

		@include media-breakpoint-up(lg) {
			flex: 0 0 18%;
		}
		@include media-breakpoint-up(xl) {
			flex: 0 0 184px;
		}
	}

	.col-time {
		// flex: 0 0 20%;
		flex: 0 0 100%;

		@include media-breakpoint-up(lg) {
			flex: 0 0 18%;
		}
		@include media-breakpoint-up(xl) {
			flex: 0 0 184px;
		}
	}

	.col-btn {
		// flex: 0 0 15%;
		flex: 0 0 100%;

		@include media-breakpoint-up(lg) {
			flex: 0 0 24%;
		}
		@include media-breakpoint-up(xl) {
			flex: 0 0 20.5%;
		}
	}

	.form-placeholder {
		color: #7f7e79;
		font-family: Lato-Regular, sans-serif;
		font-weight: 300;
		font-size: 1.7rem;
		line-height: 2.3rem;

		@include media-breakpoint-up(sm) {
			// font-weight: 400;
		}
	}
}

.col-calendar {
	.datepicker {
		cursor: pointer;
		color: transparent !important;
		text-shadow: 0 0 0 $body-color;
	}
}

.col-time {
	.dropdown > .btn {
		&:hover {
			background-color: $white;
		}
	}

	.btn {
		display: flex;
		align-items: center;
		padding-left: 1.6rem;
		padding-right: 1.6rem;

		@include media-breakpoint-up(sm) {
			// font-family: Lato-Light, sans-serif;
		}

		&:active,
		&:focus {
			outline: none;
			box-shadow: none;
		}

		&::after {
			display: none;
		}
	}

	.dropdown-menu {
		padding: 2.4rem 3.2rem;
		width: 100%;
		border: 1px solid #e4e4e4;
		margin-top: .7rem;
		border-radius: .4rem;
		box-shadow: 0 2px 4px 0 #e4e4e4;

		@include media-breakpoint-up(lg) {
			width: 176px;
		}

		.btn {
			height: 4rem;
			justify-content: center;
			line-height: 3rem;
			font-size: 1.4rem;
		}

		p {
			margin-top: .4rem;
			margin-bottom: .8rem;
			font-size: 1.3rem;
			line-height: 1.8rem;
			font-family: Lato-Light, sans-serif;
			font-weight: 300;
		}
	}
}

.col-search {
	.input-group-append img {
		opacity: 0;
		transition: .1s opacity;
	}
}

.search-input,
.data-input {
	border: transparent;

	&:focus {
		border-color: transparent;
	}

	&[disabled] {
		background-color: transparent;
	}
}

.col-search-input.form-control {
	border-right: none;

	&:focus ~ .input-group-append img {
		opacity: 1;
	}
}

.search-result {
	position: absolute;
	top: calc(100% + .8rem);
	left: .4rem;
	right: .4rem;
	border: 1px solid $b-color;
	border-radius: $b-radius;
	background-color: $white;
	box-shadow: $b-shadow;
	z-index: 10;

	&__item {
		padding: 1.2rem 1.5rem;
		display: flex;
		flex-direction: row;
		align-items: center;
		font-weight: 700;
		line-height: 2.4rem;
		text-decoration: none;
		color: initial;
		transition: .1s opacity;

		&:hover {
			background-color: #e4e4e4;
			text-decoration: none !important;
			color: inherit;

			@include media-breakpoint-up(sm) {
				background-color: #f4f4f4;
			}
		}

		&:nth-child(2n) {
			background-color: #e4e4e4;

			@include media-breakpoint-up(sm) {
				background-color: #f4f4f4;
			}
		}
	}

	&__icon {
		min-width: 1.8rem;
		flex: 0 0 auto;
	}

	&__text {
		padding-left: 1.6rem;
		word-break: break-word;
	}

	&__title {
		font-size: 1.4rem;
		font-weight: 900;
		line-height: 2rem;
		max-height: 4rem;
		overflow: hidden;
		text-overflow: ellipsis;
		margin-bottom: 0;
	}

	&__roomName {
		color: #335c67;
		font-size: 1.2rem;
		font-family: Lato-Bold, sans-serif;
		line-height: 1.7rem;
		text-transform: uppercase;
	}
}

.rates {
	font-size: 1.2rem;
	line-height: 1.6rem;
	margin-top: .2rem;

	&__rate {
		color: #ff5823;
		margin-right: .2rem;

		i,
		svg {
			color: inherit;
			fill: inherit;
		}

		img,
		i,
		svg {
			margin-right: .2rem;
		}
	}

	&__amount {
		color: $body-color;
	}

	span {
		display: inline-flex;
		vertical-align: middle;
	}
}

.modal .modal-content {
	border-radius: 0 !important;
}

.modal .search-result {
	position: relative;
	top: 0;
	left: 0;
	right: 0;
	border: none;
	border-radius: 0;
	background-color: #fff;
	box-shadow: none;
}

.dropdown-divider {
	margin: 0 2.4rem;
	position: relative;

	&.-text {
		margin: 2.4rem;
		overflow: visible;

		span {
			color: #7f7e79;
			padding: .6rem 1.6rem;
			display: inline-flex;
			background-color: $white;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}
}

.modal .dropdown-divider {
	margin: 1.6rem 0;
	border-top-color: #bfbdb6;

	@include media-breakpoint-up(sm) {
		margin: 2.4rem 0;
	}

	&.-text {
		margin: 3rem 0;

		@include media-breakpoint-up(sm) {
			margin: 2.4rem 0;
		}
	}
}

#timeModal {
	p {
		margin-top: .4rem;
		margin-bottom: .8rem;
		font-size: 1.3rem;
		line-height: 1.8rem;
		font-family: Lato-Light, sans-serif;
		font-weight: 300;
	}
}

#sendEmailModal {
	form {
		label {
			@media screen and (max-width: 767px) {
				font-size: 17px;
				line-height: 22px;
				margin-bottom: 1.6rem;
			}
		}

		p {
			font-size: 1.4rem;
			line-height: 1.7rem;
		}

		.btn {
			@media screen and (max-width: 767px) {
				width: 100%;
			}
		}

		.form-control {
			@media screen and (max-width: 767px) {
				font-size: 1.4rem;
			}
		}
	}
}
