.input-group-lg > .custom-select,
.input-group-lg > .form-control:not(textarea),
.form-control.form-control-lg {
	height: 4.8rem;
	font-size: 1.4rem;
	// font-size: 16px;
	// transform-origin: top left;
	// transform: scale(.75);
	line-height: 2.4rem;
	font-weight: 600;
	color: $body-color;
	// font-family: $font-heading, sans-serif;

	&::placeholder {
		font-weight: 300 !important;
		color: #7f7e79;
	}

	@include media-breakpoint-up(sm) {
		font-size: 1.7rem;
		// transform: none;
	}
}

.form-control.form-control-lg.data-input {
	font-size: 1.7rem;
}

.form-control,
.input-group-text {
	border-color: $b-color;
}

.form-control {
	transition: .1s border-color;

	&:focus,
	&:active {
		outline: none;
		box-shadow: none;
		border-color: #2274a5;
		transition: .1s border-color;
	}

	&::placeholder {
		font-family: Lato-Light, sans-serif;
		font-weight: 300;
		color: #7f7e79;
	}
}

.input-group-prepend .input-group-text {
	border-right: none;
}

.input-group-append .input-group-text {
	border-left: none;
}

.input-group-icon {
	position: relative;
	margin-bottom: 1.6rem;

	.input-icon {
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
	}

	&.-icon-right {
		.form-control {
			padding-right: 5rem;
			padding-left: 2.6rem;
		}

		.input-icon {
			right: 2.6rem;
		}

		&.-sm-gup {
			.form-control {
				padding-right: 4rem;
			}

			.input-icon {
				right: 1.3rem;
			}
		}
	}

	&.-icon-left {
		.form-control {
			padding-left: 5rem;
			padding-right: 2.6rem;
		}

		.input-icon {
			left: 2.6rem;
		}

		&.-sm-gup {
			.form-control {
				padding-left: 4rem;
			}

			.input-icon {
				left: 1.3rem;
			}
		}
	}

	.form-control:focus {
		border-color: #2274a5;
	}
}

textarea.form-control {
	min-height: 114px;
	padding: 1.6rem;
}

.custom-checkbox {
	.custom-control-label {
		padding-left: 1.6rem;

		&::before,
		&::after {
			width: 1.8rem;
			height: 1.8rem;
		}
	}
}

.form-control:focus {
	border-color: #2274a5;

	+ .input-group-append .input-group-text,
	.input-group-prepend .input-group-text {
		border-color: #2274a5;
		transition: .1s border-color;
	}
}

.input-group-text {
	transition: .1s border-color;
}

@media all and (max-width: 767px) {
	.input-group {
		.form-control,
		.input-group-text {
			border-color: $b-color;
		}
	}
}

.input-group.in-focus {
	.form-control,
	.input-group-text {
		border-color: #2274a5;
	}
}

.modal form label {
	color: #7f7e79;
}
