.footer {
	padding: 3.2rem 2.4rem;

	&__col {
		display: flex;
		flex-direction: column;
	}

	&__title {
		font-size: 1.7rem;
		line-height: 2.3rem;
		margin-bottom: 2.8rem;

		@include media-breakpoint-up(sm) {
			font-size: 1.4rem;
			line-height: 1.7rem;
			margin-bottom: 1.6rem;
		}
	}

	&__link {
		color: #7f7e79;
		font-size: 1.4rem;
		font-weight: 700;
		line-height: 1.5rem;
		text-decoration: none;
		display: inline-flex;
		margin-bottom: 2.8rem;

		@include media-breakpoint-up(sm) {
			margin-bottom: .8rem;
			font-size: 1.2rem;
		}

		&:hover {
			color: #7f7e79;
			text-decoration: underline;
		}
	}

	.menu-socials {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
		margin-top: 2.4rem;

		a {
			text-decoration: none;
			margin-left: 5.6rem;
			transition: .1s opacity;
			max-height: 2.8rem;
			display: inline-flex;

			&:first-child {
				margin-left: 0;
			}

			&:hover {
				opacity: .8;
			}
		}

		.fb {
			width: 2rem;
		}

		img {
			max-width: 2.8rem;
		}
	}
}
