.datepicker {
	&::after,
	&::before {
		display: none;
	}

	&.dropdown-menu {
		padding: 4rem;
		margin-top: .7rem;
		box-shadow: 0 2px 4px 0 #e4e4e4;
		border-radius: .4rem;

		td {
			height: 38px;
			width: 38px;
		}
	}

	.datepicker-switch {
		color: #252b33;
		font-family: Lato-Regular, sans-serif;
		font-size: 17px;
		font-weight: 700;
		line-height: 24px;

		&:hover {
			background-color: #f4f4f4;
		}
	}

	th {
		color: #7f7e79;
		font-family: Lato-Light, sans-serif;
		font-size: 12px;
		line-height: 15px;
		height: 32px;

		&.prev,
		&.next {
			height: 38px;
			width: 42px;
			border: 1px solid #e4e4e4;
			border-radius: 0;
			font-size: 24px;
			// opacity: 0;
			// visibility: hidden;
			// border-color: transparent;

			@include media-breakpoint-up(sm) {
				// opacity: 1;
				// visibility: visible;
				// border-color: #e4e4e4;
				vertical-align: middle;
			}

			&:hover {
				background-color: #f4f4f4;
			}

			&.disabled {
				visibility: visible;
				opacity: .5;
				pointer-events: none;
			}
		}
	}

	td {
		height: 48px;
		width: 48px;
		border: 1px solid #e4e4e4;
		border-radius: 0;

		@include media-breakpoint-up(sm) {
			height: 38px;
			width: 38px;
			font-size: 14px;
			line-height: 17px;
		}

		&:hover {
			background-color: #f4f4f4;
		}

		&.active {
			border-radius: 0;
			text-shadow: none;
			background-image: none !important;
			background-color: #ff5722 !important;

			&:hover {
				background-color: #d94a1d !important;
			}
		}
	}
}
