.btn-lg {
	font-size: 1.4rem;
	line-height: 3.6rem;
	height: 4.8rem;
	padding-left: 2.4rem;
	padding-right: 2.4rem;

	&.-text-lg {
		font-size: 1.7rem;
		font-weight: 700;
	}
}

.btn-xl {
	line-height: 4.4rem;
	height: 5.6rem;

	@include media-breakpoint-up(md) {
		line-height: 3.6rem;
		height: 4.8rem;
	}
}

.btn.btn-input {
	border-color: $b-color;
	background-color: $white;
	color: $body-color;

	&:hover {
		background-color: #f4f4f4;
	}
}

.btn.btn-outline-secondary {
	&:hover {
		background-color: #f4f4f4;
		color: #2274a5;
	}
}

.btn.btn-outline-light {
	color: #fff;
	border-color: #bdbdbd;

	&:hover {
		color: #fff;
		background-color: rgba(255, 255, 255, .15);
		border-color: #bdbdbd;
	}

	&:focus,
	&:active,
	&.active,
	&:not(:disabled):not(.disabled):active {
		background: #2274a5;
		border-color: #2274a5;
		color: #fff;
	}

	&.disabled,
	&[disabled] {
		color: #7f7e79;
		pointer-events: none;
	}
}

.btn-fb {
	height: 4.8rem;
	border: 1px solid #4569b2;
	border-radius: $b-radius;
	color: $white;
	background-color: #4569b2;
	font-size: 1.7rem;
	font-weight: 700;
	line-height: 2.4rem;
	text-align: center;
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: center;

	&:hover {
		color: $white;
		opacity: .8;
	}
}

.btn-google {
	height: 4.8rem;
	border: 1px solid #252b33;
	border-radius: $b-radius;
	background-color: $white;
	font-size: 1.7rem;
	font-weight: 700;
	line-height: 2.4rem;
	text-align: center;
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: center;

	&:hover {
		opacity: .8;
	}
}

.btn-block-xs {
	@media screen and (max-width: 546px) {
		display: block;
	}
}

.el-height-40-lg {
	@include media-breakpoint-up(md) {
		height: 4rem !important;
		line-height: 2.6rem !important;
	}

	&::placeholder {
		font-family: Lato, sans-serif !important;
		font-weight: 400;

		@include media-breakpoint-up(sm) {
			font-family: Lato-Light, sans-serif !important;
		}
	}
}
