@font-face {
	font-family: Roboto;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
	font-family: Lato;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/Lato-Regular.ttf);
}

@font-face {
	font-family: Lato-Bold;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/Lato-Bold.ttf);
}

@font-face {
	font-family: Lato-Black;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/Lato-Black.ttf);
}

@font-face {
	font-family: Lato-Light;
	font-style: normal;
	font-weight: 400;
	src: url(../fonts/Lato-Light.ttf);
}

// libraries
@import '~bootstrap-datepicker/dist/css/bootstrap-datepicker';

// variables
@import 'settings/variables';

// Global settings via bootstrap
@import 'settings/custom';

// Bootstrap modules
// all modules
// @import '~bootstrap/scss/bootstrap';
// Required
@import '~bootstrap/scss/functions';
@import '~bootstrap/scss/variables';
@import '~bootstrap/scss/mixins';

// Optional
@import '~bootstrap/scss/reboot';
@import '~bootstrap/scss/type';
@import '~bootstrap/scss/images';
@import '~bootstrap/scss/code';
@import '~bootstrap/scss/grid';
@import '~bootstrap/scss/buttons';
// @import '~bootstrap/scss/button-group';
@import '~bootstrap/scss/close';
@import '~bootstrap/scss/modal';
@import '~bootstrap/scss/dropdown';
@import '~bootstrap/scss/forms';
@import '~bootstrap/scss/custom-forms';
@import '~bootstrap/scss/input-group';
@import '~bootstrap/scss/nav';
@import '~bootstrap/scss/navbar';
@import '~bootstrap/scss/card';
@import '~bootstrap/scss/transitions';
@import '~bootstrap/scss/utilities';
// Bootstrap modules end

// Global tools
@import 'tools/functions';
@import 'tools/mixins';
@import 'tools/placeholders';
@import 'tools/animations';

// General global styles
@import 'generic/normalize';
@import 'generic/box-sizing';

// Unclassed HTML
@import 'elements/general';
@import 'elements/links';

// Designed pieces of DOM
@import 'components/header';
@import 'components/mobile-header';
@import 'components/button';
@import 'components/form';
@import 'components/footer';
@import 'components/datepicker';

// Page specific styles
@import 'pages/home';

// Utility classes, hacks, overrides
@import 'trumps/utilities';
@import 'trumps/overrides';
